import React from 'react';
import { Link } from 'react-router-dom';
import { FaHome, FaShoppingCart } from 'react-icons/fa';
import './Navbar.css';

const Navbar = ({ cartItems }) => {
    const totalItems = cartItems.reduce((total, item) => total + item.quantity, 0); // Suma todas las unidades

    return (
      <nav className="navbar">
        <div className="navbar__container">
          <Link className="nav-link" to="/"><FaHome /></Link>
          <Link className="navbar-brand" to="/">Penitentes Wines</Link>
          <Link className="nav-link cart-link" to="/cart">
            <FaShoppingCart /> <span className="cart-count">{totalItems}</span>
          </Link>
        </div>
      </nav>
    );
};

export default Navbar;